<template>
    <b-card >
        <template #header>
            <h2 class="mb-0">Assign Package</h2>

        </template>
        <b-card-body>
            <b-form ref="form" @submit.prevent="submit()">
                <b-row>
                    <b-col sm="6">
                        <div v-if="!isQuery">
                        <label>Search Order</label>
                        <b-input v-model="key" placeholder="Search Order" ></b-input>
                        </div>

                        <div v-if="loading.searchOrders" class="text-center">
                            <b-spinner type="grow"></b-spinner>
                        </div>
                        <div v-if="!isQuery">
                            <label class="mt-3">Select Order</label>
                            <b-form-select :disabled="orders.length < 1" v-if="!loading.searchOrders" v-model="info.order_id" :options="orders" value-field="id"  text-field="title">
                                <b-select-option value="" hidden>Select Order ({{orders.length}})</b-select-option>
                            </b-form-select> 
                        </div>

                        <div class="text-center pt-4" v-if="loading.selectedOrder">
                            <b-spinner style="width:10rem;height:10rem" type="grow"></b-spinner>
                        </div>
                        <b-card class="mt-3" v-if="!loading.selectedOrder && details">

                            <template #header>
                                <h2 class="mb-0">Order details</h2>

                            </template>

                            <b-row >
                                <b-col sm="12">
                                    <b-img style="max-height:25rem;object-fit: contain;" class="w-100" v-for="img in details.image" :key="img.id" :src="img.image_path" :title="details.title"></b-img>
                                </b-col>
                                <b-col sm="12" class="d-flex align-items-center">
                                    <b-row>
                                    <b-col sm="4" class="text-right text-gery">
                                        Title:
                                    </b-col>
                                    <b-col sm="8" class="h2 mb-0">
                                        {{details.title}}
                                    </b-col>

                                    <b-col sm="4" class="text-right text-gery">
                                        Price:
                                    </b-col>
                                    <b-col sm="8" class="h3 mb-0">
                                        {{details.price}}
                                    </b-col>

                                    <b-col sm="4" class="text-right text-gery">
                                        Description:
                                    </b-col>
                                    <b-col sm="8">
                                        {{details.description}}
                                    </b-col>



                


                                    <b-col sm="4" class="text-right text-gery">
                                        Status:
                                    </b-col>
                                    <b-col sm="8">
                                        <span
                                        :class="{'text-success':details.status == 'completed',
                                        'text-info':details.status == 'processing',
                                        'text-warning': details.status == 'review'}"
                                        >{{details.status}}</span>
                                    </b-col>

                                    <b-col sm="4" class="text-right text-gery">
                                        Payment Method:
                                    </b-col>
                                    <b-col sm="8">
                                        {{details.payment_method.title}}
                                    </b-col>


                                    <b-col sm="4" class="text-right text-gery">
                                        Created At:
                                    </b-col>
                                    <b-col sm="8">
                                        {{details.created_at | moment('from', 'now')}}
                                    </b-col>
                                    </b-row>
                                </b-col>


                
                                <b-col sm="12">
                                    <b-card class="mt-4">
                                    <template #header>
                                        <h3>User Data</h3>
                                    </template>
                                    <b-row v-if="details.user">
                                        
                                        <b-col sm="5" class="text-right text-gery">
                                            Name:
                                        </b-col>
                                        <b-col sm="7">
                                            {{`${details.user.first_name} ${details.user.last_name}`}}
                                        </b-col>

                                        <b-col sm="5" class="text-right text-gery">
                                            Phone:
                                        </b-col>
                                        <b-col sm="7">
                                        {{`(+${details.user.phone_code}) ${details.user.phone}`}}
                                        </b-col>

                                        <b-col sm="5" class="text-right text-gery">
                                            Email:
                                        </b-col>
                                        <b-col sm="7">
                                        {{details.user.email}}
                                        </b-col>

                                        <b-col sm="5" class="text-right text-gery">
                                            Location:
                                        </b-col>
                                        <b-col sm="7">
                                            {{`${details.user.country.name}, ${details.user.state.name}, ${details.user.city.name}`}}
                                        </b-col>


                                    </b-row>
                                    </b-card>
                                </b-col>

            
                                <b-col sm="12">
                                    <b-card class="mt-4">
                                    <template #header>
                                        <h3>Recipient Data</h3>
                                    </template>
                                    <template v-for="rec in details.recipient">
                                        <b-row :key="rec.id">

                                        <b-col sm="5" class="text-right text-gery">
                                            Name:
                                        </b-col>
                                        <b-col sm="7">
                                            {{`${rec.first_name} ${rec.last_name}`}}
                                        </b-col>

                                        <b-col sm="5" class="text-right text-gery">
                                            Phone:
                                        </b-col>
                                        <b-col sm="7">
                                            {{`(+${rec.phone_code}) ${rec.phone}`}}
                                        </b-col>

                                        <b-col sm="5" class="text-right text-gery">
                                            Address:
                                        </b-col>
                                        <b-col sm="7">
                                            {{rec.address}}
                                        </b-col>

                                        <b-col sm="5" class="text-right text-gery">
                                            Location:
                                        </b-col>
                                        <b-col sm="7">
                                            {{`${rec.country.name}, ${rec.state.name}, ${rec.city.name}`}}
                                        </b-col>
                                        </b-row>

                                    </template>
                                    
                                </b-card>
                            </b-col>
                            </b-row>

                        </b-card>
                    </b-col>
                    <b-col sm="6">
                        <label>Driver</label>
                        <b-form-select v-model="info.user_id">
                            <b-select-option hidden value="">Select Driver</b-select-option>
                            <b-select-option v-for="user in deliveries" :key="user.id" :value="user.id">{{`${user.first_name} ${user.last_name}`}}</b-select-option>
                        </b-form-select>
                        <div class="d-flex w-100">
                            <b-button variant="success" :disabled="loading.submit" type="submit" class="w-25 mt-3 ml-auto mr-3">
                                <span v-if="!loading.submit">Submit</span>
                                <b-spinner v-if="loading.submit" small type="grow"></b-spinner>
                            </b-button>
                        </div>
                    </b-col>
                </b-row>

            </b-form>

        </b-card-body>

    </b-card>

</template>
<script>
import axios from "axios"
import {showToast } from '@/assets/js/index';
export default {
    name:"NewPackge",
    data() {
        return {
            info:{
                origin_latitude:26.8206,
                origin_longitude:30.8025,
                user_id:"",
                order_id:"",
                status:"enroute"
            },
            key:"",
            orders:[],
            deliveries:[],
            details:"",
            loading:{
                submit:false,
                users:false,
                searchOrders:false,
                selectedOrder:false
            }
        }
    },
    computed:{
        isQuery() {
            if(this.$route.query.orderId) {
                return true
            } else {
                return false
            }
        }
    },
    methods:{
       submit() {
            let keys = Object.keys(this.info)
            let formData = new FormData();

            for(let i of keys) {
                formData.append(i, this.info[i])
            }
            this.loading.submit = true
           axios.post(`/admin/v1/packages`, this.info).then(res=>{
               this.loading.submit = false;
               showToast('success', `Success`, `Packge has been created`, this)
           })
           .catch(err=>{
               this.loading.submit = false;
                for(let error in err.response.data.errors) {
                    showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
                }
                console.log(err.response)
           })
        },
        getDelivaries() {
  
            axios.get('/admin/v1/roles?role=delivery')
            .then((res) => {

                this.deliveries = res.data.data[0].user
            })
            .catch((error) => {
          
                console.error(error)
            })

        },
    },
    mounted() {
        this.getDelivaries();
        if(this.$route.query.orderId) {
            this.info.order_id = this.$route.query.orderId
        }
    },
    watch:{
        "key":function() {
            this.loading.searchOrders = true
            axios.get(`/admin/v1/orders/search/${this.key}`)
            .then(res=>{
                this.orders = res.data.data
                this.loading.searchOrders = false
            
            })
            .catch(err=>{
                this.loading.searchOrders = false
            })
        },
        "info.order_id":function() {
            this.loading.selectedOrder = true
            axios.get(`admin/v1/orders/${this.info.order_id}`)
            .then(res=>{
                this.loading.selectedOrder = false
                this.details = res.data.data
            })
            .catch(()=>{
                this.loading.selectedOrder = false
            })
        }
    }
}
</script>